// Importaciones con ?url para que Parcel genere las rutas finales correctas
import food1 from "../img/mobile/food1.jpg?url";
import food2 from "../img/mobile/food2.jpg?url";
import food3 from "../img/mobile/food3.jpg?url";

document.addEventListener("DOMContentLoaded", () => {
  // Tema
  document.getElementById("changeTheme")?.addEventListener("click", () => {
    document.body.classList.toggle("dark-theme");
  });

  // Idioma
  document.getElementById("changeLanguage")?.addEventListener("click", () => {
    document.documentElement.lang =
      document.documentElement.lang === "es" ? "en" : "es";
  });

  // Menú móvil
  const menuToggle = document.querySelector(".menuToggle");
  const menu = document.getElementById("menu");
  menuToggle?.addEventListener("click", () => {
    menu?.classList.toggle("hidden");
  });

  // Carrusel con <img> solamente
  const images = [
    { src: food1, alt: "Plato internacional 1" },
    { src: food2, alt: "Plato internacional 2" },
    { src: food3, alt: "Plato internacional 3" },
  ];

  let currentIndex = 0;
  const carouselImage = document.getElementById("carouselImage");
  const prevButton = document.getElementById("prevImage");
  const nextButton = document.getElementById("nextImage");

  function updateCarousel(index) {
    const image = images[index];
    if (carouselImage) {
      carouselImage.src = image.src;
      carouselImage.alt = image.alt;
    }
  }

  prevButton?.addEventListener("click", () => {
    currentIndex = (currentIndex - 1 + images.length) % images.length;
    updateCarousel(currentIndex);
  });

  nextButton?.addEventListener("click", () => {
    currentIndex = (currentIndex + 1) % images.length;
    updateCarousel(currentIndex);
  });

  updateCarousel(currentIndex); // Inicial

  // Formulario
  const reservationForm = document.getElementById("reservationForm");
  reservationForm?.addEventListener("submit", (e) => {
    e.preventDefault();
    alert("Reserva realizada con éxito");
  });
});
